.talkbubble1 {
  height: 200px;
  width: 280px;
  padding: 20px;
  position: relative;
  border-radius: 20px;
  background-color: #5571a5;
  @media (min-width: 768px) {
    width: 400px;
  }
}
.talkbubble1:before {
  content: "";
  position: absolute;
  right: 100%;
  top: 130px;
  width: 0;
  height: 0;
  border-top: 20px solid transparent;
  border-right: 30px solid #5571a5;
  border-bottom: 20px solid transparent;
}

.talkbubble2 {
  height: 200px;
  width: 280px;
  padding: 20px;
  position: relative;
  border-radius: 20px;
  background-color: #5e3e70;
  @media (min-width: 768px) {
    width: 400px;
  }
}
.talkbubble2:before {
  content: "";
  position: absolute;
  left: 100%;
  top: 130px;
  width: 0;
  height: 0;
  border-top: 20px solid transparent;
  border-right: 30px solid #5e3e70;
  border-bottom: 20px solid transparent;
  transform: rotate(180deg);
}
